
#app{
    min-height: 1000px;
}
.pageleft{
    width: 200px;
    position: fixed;
    top: 0;
    left: 0px;
    overflow-x: hidden;
    overflow-y: auto;
    padding:20px;
    background-color: #013A77;
    min-height: 1000px;
}
.pageright{
    margin-left: 240px;
    background:url('assets/bg.jpg') repeat-y;
    min-height: 1000px;
    background-size: cover;
}
.nav{
    color: #fff;
}
.nav .cat1{
    font-size: 25px;
    font-weight: bold;

}
.nav .cat2 a{
    display: inline-block;
    padding-top: 15px;
    font-size: 18px;
    color: #fff;
}
.nav .cat2 a:hover{
   text-decoration: underline;
}



